.vetskitchen-tabs {
  margin-bottom: 30px;
}
.vetskitchen-tabs .nav-tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 2px;
}
.vetskitchen-tabs .nav-tabs .nav-link {
  padding: 10px 15px;
  flex-grow: 1;
  color: #ffffff;
  text-align: center;
  background-color: #018077;
  border: 1px solid #018077;
  border-radius: 4px 4px 0 0;
}
.vetskitchen-tabs .nav-tabs .nav-link.active {
  background-color: #ff8f02;
  border: 1px solid #ddd;
}
.vetskitchen-tabs .tab-content {
  padding-top: 10px;
}

body:not(.wp-admin) .vetskitchen-tabs .tab-content .tab-pane {
  display: none;
}
body:not(.wp-admin) .vetskitchen-tabs .tab-content .tab-pane.active {
  display: block;
}